.title-skill{
    color:rgb(155, 155, 155);
    font-size:44px;
    text-align: center;
    margin: -200px 0 -80px;
}
.cont-imgs-skill{
    width:70%; 
    flex-wrap: wrap;
    gap: 20px;
    /* height:500px; */
    display: flex;
    align-self: center;
    margin-top: -100px;
    justify-content: space-around;
}
.cont-imgs-skill .img-skill{
    width:100px;
    height:100px;
    margin: 0px;
    align-items: center;
    align-self: center;
    align-content: center;
    margin: auto;
    position: relative;
}

.cont-imgs-skill .img-skill[data-descr]:hover::after{
    content: attr(data-descr);
    position: absolute;
    left: -55px;
    top: 100px;
    min-width: 200px;
    max-width: 300px;
    border: 1px #383838 solid;
    border-radius: 10px;
    background-color: #9a9ab0;
    padding: 12px;
    color: #000000;
    font-size: 14px;
    z-index: 1;
}

.img-skill:nth-child(1){
    background:url('../Images/html.png');
    background-size:contain;
    background-repeat: no-repeat;
}

.img-skill:nth-child(2){
    background:url('../Images/css.png');
    background-size: contain;
    background-repeat: no-repeat;

}
.img-skill:nth-child(3){
    background:url('../Images/js.png');
    background-size: contain;
    background-repeat: no-repeat;

}
.img-skill:nth-child(4){
    background:url('../Images/react.png');
    background-size: contain;
    background-repeat: no-repeat;

}
.img-skill:nth-child(5){
    background:url('../Images/git.png');
    background-size: contain;
    background-repeat: no-repeat;

}
.img-skill:nth-child(6){
    background:url('../Images/github.png');
    background-size: contain;
    background-repeat: no-repeat;

}


@media screen and (max-width: 500px){
    .title-skill{
        font-size: 31px;
        margin: -10px 0 100px;
    }

    .cont-imgs-skill .img-skill{
        /* outline: 1px solid black; */
        width:80px;
        height:80px;
    }

    .cont-imgs-skill .img-skill[data-descr]:hover::after{
        min-width: 120px;
        max-width: 130px;
        font-size: 12px;
        top: 82px;
        left: -31px;
    }
}